.Navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(grey), to(#202020));
  background: -webkit-linear-gradient(grey, #202020);
  background: -o-linear-gradient(grey, #202020);
  background: linear-gradient(grey, #202020);
}

.Navigation li {
  display: inline-block;
  margin: 5px 40px;
}

.Navigation a {
  text-decoration: none;
  color: white;
}

.Navigation a:hover,
.Navigation a:active {
  color: #fa923f;
}

.imgHover {
  -webkit-transition: all .1s;
  -o-transition: all .1s;
  transition: all .1s;
}

.imgHover:hover {
  -webkit-transform: scale(1.04);
      -ms-transform: scale(1.04);
          transform: scale(1.04);
}

@media screen and (max-width: 500px) {
  .Navigation li {
    margin: 5px 10px;
  }
}



